import React, { Component } from 'react'
import Tag from './Tag'
import './index.less'

export default class Tags extends Component {
  render() {
    return (
      <div className="Tags">
        <div className="Tags-title">
          <span>Tags</span>
          <div className="Tags-title-dividing"></div>
        </div>
        <div className="Tags-title-content">
          <Tag name="Linked List"/>
          <Tag name="Array"/>
          <Tag name="Jump List"/>
          <Tag name="Algorithm"/>
        </div>
      </div>
    )
  }
}
