import React, { Component } from 'react'
import BlogItem from '@/components/BlogItem'
import UserCard from '@/components/UserCard'
import Tags from '@/components/Tags'
import './index.less'

import gril2 from '@/assets/img/gril2.png'
import balloon from '@/assets/img/热气球.jpg'
import weather from '@/assets/cutImg/天气.png'

export default class Blog extends Component {
  render() {
    return (
      <div className="blog">
        {/* <div className="blog-container"> */}
          <div className="blog-header">
            {/* 左侧 */}
            <div className="blog-header-left">
              <img src={gril2} alt=""/>
            </div>
            {/* 右侧 */}
            <div className="blog-header-right">
              {/* 上面 音乐播放 */}
              <div className="blog-header-right-top">
                {/* 音乐播放背景 */}
                <img src={balloon} alt=""/>
                {/* 渐变遮罩 */}
                <div className="blog-header-right-top-mask"></div>
                {/* 音乐 操作 */}
                <div className="blog-header-right-top-music">
                  {/* 音乐信息 */}
                  <div className="music-info">
                    <div className="music-info-title">只道寻常</div>
                    <div className="music-info-name">谢春花</div>
                  </div>
                  {/* 音乐操作 */}
                  <div className="music-handle">
                    <i className="iconfont icon-ai10"></i>
                    <div className="music-center">
                      <i className="iconfont icon-zanting"></i>
                    </div>
                    <i className="iconfont icon-ai09"></i>
                  </div>
                </div>
              </div>
              {/* 下面 天气 和 日历 */}
              <div className="blog-header-right-bottom">
                <div className="blog-header-right-bottom-weather">
                  {/* 天气图标 */}
                  <div className="blog-header-right-bottom-weather-icon">
                    <img src={weather} alt=""/>
                  </div>
                  {/* 地址 和 温度 */}
                  <div className="blog-header-right-bottom-weather-data">
                    {/* 地址 */}
                    <div className="blog-header-right-bottom-weather-data-position">
                      <div className="position-name">广州市</div>
                      <div className="position-subTitle">阴，多云</div>
                    </div>
                    {/* 温度 */}
                    <div className="blog-header-right-bottom-weather-data-temperature">
                      30°C
                    </div>
                  </div>
                </div>
                {/* 日历 */}
                <div className="blog-header-right-bottom-date">
                  <div className="blog-date-time">
                    16:49
                  </div>
                  <div className="blog-date-day">
                    <div className="blog-date-day-week">
                      星期四
                    </div>
                    <div className="blog-date-day-detail">
                      01 四月 2021
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>  
          <div className="blog-main">
            <div className="blog-main-title">
              <i className="iconfont icon-16"></i>
              <span>Blog</span>
              <div className="blog-main-title-dividing"></div>
            </div>
            {/* 博客列表 */}
            <div className="blog-main-content">
              <div className="blog-main-list">
                <BlogItem/>
                <BlogItem/>
                <BlogItem/>
                <BlogItem/>
                <BlogItem/>
                <BlogItem/>
              </div>  
              <div className="blog-main-user">
                <div className="blog-main-user-container">
                  <UserCard />
                  <Tags/>
                </div>
              </div>
            </div>
          </div>
        {/* </div> */}
      </div>
    )
  }
}
