import React, { Component } from 'react'
import './index.less'

import avatar from '@/assets/img/avatar.jpg'

export default class UserCard extends Component {
  render() {
    return (
      <div className="UserCard">
        <div className="UserCard-content">
          <img src={avatar} alt=""/>
          <div className="UserCard-content-name">
            <span>大正</span>
            <div className="UserCard-content-name-baseLine"></div>
          </div>
          <div className="UserCard-content-text">好好学习，天天向上</div>
          <div className="UserCard-content-connect">
            <i className="iconfont icon-weixin" data-after="PPmaka"></i>
            <i className="iconfont icon-qq" data-after="924370430"></i>
            <i className="iconfont icon-dianhua" data-after="17620953973"></i>
            <i className="iconfont icon-github" data-after="DaZheng-Coder"></i>
          </div>
          <div className="UserCard-content-data">
            <div className="UserCard-content-data-item">
              <span>1k</span>
              <span className="text-opacity">Words</span>
            </div>
            <div className="UserCard-content-data-item">
              <span>32</span>
              <span className="text-opacity">Articles</span>
            </div>
            <div className="UserCard-content-data-item">
              <span>13</span>
              <span className="text-opacity">Categories</span>
            </div>
            <div className="UserCard-content-data-item">
              <span>42</span>
              <span className="text-opacity">Tags</span>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
