import React, { Component } from 'react'
import './index.less'

import girl from '@/assets/img/girl.jpg'
import avatar from '@/assets/img/avatar.jpg'

export default class BlogItem extends Component {
  render() {
    return (
      <div className="BlogItem">
        {/* 图片 */}
        <div className="BlogItem-img">
          <img src={girl} alt=""/>
          <div className="BlogItem-img-mask"></div>
        </div>
        {/* 内容 */}
        <div className="BlogItem-content">
          {/* 标签 */}
          <div className="BlogItem-content-tags">
            FRONTEND
          </div>
          {/* 标题 */}
          <div className="BlogItem-content-title">
            Promise的实现
          </div>
          {/* 简略内容 */}
          <div className="BlogItem-content-shortMain">
            浏览器工作原理是一块非常重要的内容，我们经常看到的重绘，重排或者一些讲解css属性的时候，都会用到一些浏览器工作原理的知识来讲解
          </div>
        </div>
        {/* 发布者信息 */}
        <div className="BlogItem-publisher">
          {/* 发布者头像 */}
          <div className="BlogItem-publisher-avatar">
            <img src={avatar} alt=""/>
          </div>
          {/* 发布者名称 */}
          <div className="BlogItem-publisher-name">
            Da-Zheng
          </div>
          {/* 发布日期 */}
          <div className="BlogItem-publisher-date">
            2020.4.1
          </div>
        </div>
      </div>
    )
  }
}
